import React, { useEffect, useRef, useState } from "react";
import styled from "@emotion/styled";
import { Toast, Loader } from "monday-ui-react-core";
import ReactModal from "react-modal";
import { keyBy } from "lodash";
import "interceptors/axios";
import "monday-ui-react-core/dist/main.css";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

import EditCharacterModal from "components/EditCharacterModal";
import CharacterList from "components/CharacterList";
import { fetchCharacters, updateCharacter } from "services/character-service";
import { fetchWorkspaceIds } from "services/board-service";
import { checkUserAuthorize, fetchAccount } from "services/user-service";
import { useMondayContext } from "utils/monday";
import { useWebsocket } from "utils/websocket";
import { ThemeProvider } from "@emotion/react";
import { darkMode, lightMode } from "theme/theme";
import Color from "theme/colors";
import NewUserSlider from "components/NewUserSlider";

const StyledApp = styled("div")((props) => ({
    padding: 16,
    display: "flex",
    justifyContent: "center",
    boxSizing: "border-box",
    color: props.theme.textColor
}));

const StyledToast = styled(Toast)({
    zIndex: 1
});

const TextStyled = styled("h3")({
    textAlign: "center",
    width: "500px",
    margin: "auto",
    paddingTop: "30px"
});

function App() {
    const ref = useRef();
    const [workspaceIds, setWorkspaceIds] = useState([]);
    const [account, setAccount] = useState();
    const [characters, setCharacters] = useState([]);
    const [loading, setLoading] = useState(true);
    const [isAccess, setIsAccess] = useState(true);
    const [hasCharacter, setHasCharacter] = useState(true);
    const context = useMondayContext();

    const socket = useWebsocket();

    const [modal, setModal] = useState({
        open: false,
        data: undefined
    });

    const [toast, setToast] = useState({
        shown: false,
        type: "",
        message: "default"
    });

    const handleCloseModal = () => {
        setModal({
            open: false
        });
    };

    const handleOpenModal = (character) => {
        setModal({
            open: true,
            data: { character }
        });
    };

    const handleSubmit = async (form) => {
        try {
            const { id, name, avatar } = form;
            await updateCharacter(id, { name, avatar });

            setToast({
                shown: true,
                type: Toast.types.POSITIVE,
                message: "Character updated"
            });

            const characters = await fetchCharacters(account?.id, workspaceIds, { contextUser: context.user });
            setCharacters(characters);

            handleCloseModal();
        } catch (error) {
            setToast({
                shown: true,
                type: Toast.types.NEGATIVE,
                message: error.message || "Internal Server Error"
            });
        }
    };

    useEffect(() => {
        if (ref?.current) {
            ReactModal.setAppElement(ref.current);
        }
    }, [ref]);

    useEffect(() => {
        (async function () {
            try {
                setLoading(true);
                const isViewOnly = await checkUserAuthorize();

                if (!isViewOnly) {
                    const account = await fetchAccount();
                    setAccount(account);
                } else {
                    setIsAccess(false);
                }
            } catch (error) {
                console.error(error);
            } finally {
                setLoading(false);
            }
        })();
    }, []);

    useEffect(() => {
        (async () => {
            try {
                setLoading(true);

                if (account && context.boardIds?.length) {
                    const workspaceIds = await fetchWorkspaceIds(context.boardIds, { accountId: account.id });
                    setWorkspaceIds(workspaceIds);
                } else {
                    setWorkspaceIds([]);
                }
            } catch (err) {
                console.error(err);
            } finally {
                setLoading(false);
            }
        })();
    }, [account, context.boardIds]);

    useEffect(() => {
        (async () => {
            try {
                setLoading(true);
                if (account && workspaceIds?.length) {
                    const characters = await fetchCharacters(account.id, workspaceIds, { contextUser: context.user });
                    setCharacters(characters);
                } else {
                    setCharacters([]);
                }
            } catch (err) {
                console.error(err);
            } finally {
                setLoading(false);
            }
        })();
    }, [context.user, account, workspaceIds]);

    useEffect(() => {
        if (socket && account && workspaceIds.length) {
            socket.emit("join_workspace", account.id, workspaceIds);
        }
    }, [socket, account, workspaceIds]);

    useEffect(()=> {
        if (characters && workspaceIds.length && context.boardIds?.length && !characters.length) {
            setHasCharacter(false);
        } 
        
    },[characters]);

    useEffect(() => {
        if (socket) {
            socket.on("characters_earn_xp", async (message) => {
                try {
                    if (account && workspaceIds?.length) {
                        const earnings = keyBy(message, "characterId");

                        const characters = await fetchCharacters(account.id, workspaceIds, { contextUser: context.user });

                        setCharacters(
                            characters.map((character) => {
                                const earning = earnings[character.id];

                                if (earning) {
                                    return {
                                        ...character,
                                        earning
                                    };
                                }

                                return character;
                            })
                        );
                    }
                } catch (err) {
                    console.error(err);
                }
            });
        }

        return () => socket?.off("characters_earn_xp");
    }, [context.user, account, workspaceIds, socket]);
    return (
        <>
            <div>hi</div>
            <ThemeProvider theme={context.theme === "light" ? lightMode : darkMode}>
                <StyledApp ref={ref}>
                    {loading ? (
                        <div style={{ color: context.theme === "light" ? Color.PRIMARY : "#fff", fontSize: "50px" }}>
                            <Loader size={40} />
                        </div>
                    ) : (
                        isAccess ?
                            <>
                                {!hasCharacter && <NewUserSlider />}

                                {characters.length > 0 && (
                                    <CharacterList
                                        characters={characters}
                                        onEditCharacter={handleOpenModal}
                                    />
                                )}

                                {modal.open && (
                                    <EditCharacterModal
                                        character={modal.data.character}
                                        onClose={handleCloseModal}
                                        onSubmit={handleSubmit}
                                    />
                                )}

                                {toast.shown && (
                                    <StyledToast
                                        open
                                        type={toast.type}
                                        autoHideDuration={3500}
                                        onClose={() => {
                                            setToast({ shown: false });
                                        }}>
                                        {toast.message}
                                    </StyledToast>
                                )}
                            </> :
                            <TextStyled>As a view only user, you&apos;re unable to use TaskMagic. Please request Member access from your Admin to use this app !</TextStyled>
                    )
                    }
                </StyledApp>
            </ThemeProvider>
        </>
    );
}

export default App;
