import axios from "axios";
import { find } from "lodash";

import { fetchUsers } from "./user-service";

/**
 * @param {number} workspaceIds 
 * @param {{ contextUser: object }} options 
 * @returns 
 */
export async function fetchCharacters(accountId, workspaceIds = [], options) {
    const { contextUser } = options;
    const users = await fetchUsers();

    const { data } = await axios.get("/character/list", {
        params: {
            mondayAccountId: accountId,
            mondayWorkspaceIds: workspaceIds
        }
    });

    return data.characters.map((character) => ({
        ...character,
        user: find(users, { id: character.mondayUserId }),
        isEditable: character.mondayUserId === Number(contextUser.id)
    }));
}

/**
 * @param {string} id 
 * @param {{ name: string, avatar: string }} body 
 */
export async function updateCharacter(id, body) {
    return await axios.post(`/character/update/${id}`, body);
}

