import { PUBLIC_URL } from "config";
import PropTypes from "prop-types";
import { sliderImageList } from "config/constants";
import { MoveArrowLeft, MoveArrowRight } from "monday-ui-react-core/dist/allIcons";
import React from "react";
import Slider from "react-slick";
import { NextArrow, PrevArrow, SliderItem, SliderWrap } from "./styled";



function CustomNextArrow(props) {
    const { onClick } = props;
    return (
        <NextArrow onClick={onClick}>
            <MoveArrowRight/> 
        </NextArrow>
    );
}
function CustomPrevArrow(props) {
    const { onClick } = props;
    return (
        <PrevArrow onClick={onClick}  >
            <MoveArrowLeft/> 
        </PrevArrow>
    );
}

export default function NewUserSlider() {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        nextArrow: <CustomNextArrow />,
        prevArrow: <CustomPrevArrow />
    };

    return (
        <div style={{ width:"100%"}} >
            
            <SliderWrap>
                <Slider {...settings}>
                    {sliderImageList.map((image, index)=> 
                        <SliderItem key={index} >
                            <img  src={`${PUBLIC_URL}/assets/slider/${image}`} alt="" />
                        </SliderItem>
                    )}
                </Slider>
            </SliderWrap>
        </div>
    );
}

CustomNextArrow.propTypes = {
    onClick: PropTypes.func
};

CustomPrevArrow.propTypes = {
    onClick: PropTypes.func
};
