import { useEffect, useState } from "react";
import { isEqual } from "lodash";
import mondaySdk from "monday-sdk-js";

const monday = mondaySdk();

export function useMondayContext() {
    const [user, setUser] = useState();
    const [boardIds, setBoardIds] = useState([]);
    const [theme, setTheme] = useState();

    useEffect(() => {
        monday.listen("context", ({ data }) => {
            if (data) {
                setUser((user) => {
                    return (data.user && !isEqual(user, data.user))
                        ? data.user
                        : user;
                });

                setBoardIds((boardIds) => {
                    return (data.boardIds && !isEqual(boardIds, data.boardIds))
                        ? data.boardIds
                        : boardIds;
                });

                setTheme((theme) => {
                    return (data.theme && !isEqual(theme, data.theme))
                        ? data.theme
                        : theme;
                });
            }
        });
    }, []);

    return {
        user,
        boardIds,
        theme
    };
}

export default monday;
