import React from "react";
import PropTypes from "prop-types";
import { LinearProgressBar, Tooltip, Icon, Counter } from "monday-ui-react-core";
import SettingsIcon from "monday-ui-react-core/dist/icons/Settings";
import Color from "theme/colors";
import {
    Avatar,
    EarningXP,
    IconWrapper,
    Layout,
    Name,
    TooltipWrapper,
    LevelWrapper
} from "./styled";
import styled from "@emotion/styled";

const StyleLinearProgressBar = styled(LinearProgressBar)((props) => ({
    "& > div ": {
        border: `1px solid ${props.theme.borderColor}`,
        borderRadius: "4px",
        boxSizing: "border-box",
        background: props.theme.progressBackground
    }
}));

function CharacterName({ character }) {
    let name = character.name || character.user?.name;

    // if (character.user?.is_guest) {
    //     name = `Guest: ${name}`;
    // }

    // if (!character.name) {
    //     return (
    //         <i>{name}</i>
    //     );
    // }

    return (
        <>{name}</>
    );
}

function Character({ character, onEditCharacter }) {
    const multiValues = [
        {
            value: character.xp,
            color: Color.PRIMARY
        }
    ];

    return (
        <div>
            <Layout>
                <Name>
                    <CharacterName character={character} />
                    {character.isEditable && (
                        <IconWrapper>
                            <Icon
                                icon={SettingsIcon}
                                ignoreFocusStyle={false}
                                iconType={Icon.type.SVG}
                                iconSize={16}
                                onClick={() => onEditCharacter(character)}
                            />
                        </IconWrapper>
                    )}
                </Name>
                <Avatar avatar={character.avatar || "MaleSpearman1.png"} />
                <LevelWrapper>
                    <Counter count={character.level || 1} kind={Counter.kinds.LINE} />
                </LevelWrapper>
                <TooltipWrapper>
                    <Tooltip
                        style={{ textAlign: "center" }}
                        content={<div >Current Level XP <br /> {character.xp} / {character.levelXp}</div>}
                        position={Tooltip.positions.BOTTOM}>
                        <StyleLinearProgressBar
                            className='linear-progress-bar_small-wrapper'
                            size='large'
                            max={character.levelXp}
                            value={character.xp}
                            multi multiValues={multiValues}
                        />
                    </Tooltip>
                </TooltipWrapper>
                {character.earning && (
                    <EarningXP key={Date.now()}>+ {character.earning.xp} XP</EarningXP>
                )}
            </Layout>
        </div>
    );
}

Character.propTypes = {
    character: PropTypes.object,
    onEditCharacter: PropTypes.func
};

CharacterName.propTypes = {
    character: PropTypes.object
};

export default Character;
