import styled from "@emotion/styled";
import { keyframes } from "@emotion/react";
import Color from "theme/colors";
import CharacterAvatar from "components/CharacterAvatar";

export const Layout = styled("div")({
    height: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    position: "relative"
});

const earningFadeIn = keyframes({
    "0%": {
        opacity: 0,
        transform: "translate3d(0, 100%, 0) scale(0.1)"
    },

    "20%, 80%": {
        opacity: 1,
        transform: "translate3d(0, 0, 0)"
    },

    "100%": {
        opacity: 0,
        transform: "scale(0.1)"
    }
});

export const EarningXP = styled("div")({
    position: "absolute",
    top: "10%",
    right: "-16px",
    color: "#fc9e36",
    fontSize: "16px",
    textAlign: "center",
    animation: `${earningFadeIn} 3s ease`,
    animationFillMode: "forwards"
});

export const Name = styled("div")({
    cursor: "default",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    paddingBottom: "20px"
});

export const Avatar = styled(CharacterAvatar)({
    objectFit: "contain",
    width: "auto",
    height: "auto",
    maxWidth: "100%",
    maxHeight: "150px",
    margin: "auto auto 0",
    paddingBottom: "20px"
});

export const TooltipWrapper = styled("div")({
    width: "100%",

    "& > span": {
        width: "100%"
    }
    // "& > span > div > div": {
    //     border: "1px solid white",
    //     borderRadius: "4px",
    //     boxSizing: "border-box"
    // }

});

export const EditButton = styled("div")({
    cursor: "pointer",
    minWidth: "60px",
    margin: "10px 0"
});

export const IconWrapper = styled("div")({
    marginLeft: "10px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
});
export const LevelWrapper = styled("div")({
    position: "absolute",
    bottom: "20px",
    left: "0",
    "& > span > div": {
        boxSizing: "border-box",
        color: `${Color.COUNTER_COLOR}!important`,
        background: Color.COUNTER_COLOR,
        padding: "0px!important"
    },
    "& > span > div > span": {
        color: "#111111",
        fontWeight: "bold",
        textShadow: "-2px 2px 1px #0000002e"

    }
});
