import { reduce, uniq, isNull } from "lodash";

import monday from "utils/monday";

export async function fetchWorkspaceIds(boardIds) {
    const query = `query($boardIds: [Int]) {
        boards (ids: $boardIds) {
            workspace_id
        }
    }`;

    const variables = { boardIds };
    const { data } = await monday.api(query, { variables });

    const parseWorkspaceIds = () => {
        return reduce(
            data.boards,
            (acc, board) => {
                const id = board?.workspace_id;

                if (Number.isInteger(id)) {
                    return [...acc, id];
                }

                if (isNull(id)) {
                    return [...acc, 0];
                }

                return acc;
            },
            []
        );
    };

    return uniq(
        parseWorkspaceIds()
    );
}
