import axios from "axios";
import monday from "utils/monday";

export async function fetchUsers() {
    const query = `query {
        users {
            id
            name
            is_guest
            photo_small
        }
    }`;

    const { data } = await monday.api(query);
    return data.users;
}

export async function fetchAccount() {
    const query = `query {
        me {
            id
            account {
                id
            }
        }
    }`;

    const { data } = await monday.api(query);
    return data.me?.account;
}
export async function checkUserAuthorize () {
    const { data } = await axios.get("/user/authorize");
    return data.isViewOnly;
}
