import styled from "@emotion/styled";

import { Button } from "monday-ui-react-core";

export const Layout = styled("div")({
    display: "flex",
    flexDirection: "column"
});

export const ModalHeader = styled("div")({
    height: 64,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    background: "white",
    boxShadow: "0px 2px 8px rgb(0 0 0 / 8%)",
    boxSizing: "border-box",
    position: "sticky",
    top: 0,
    zIndex: 1
});

export const ModalFooter = styled("div")({
    height: 64,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    background: "white",
    boxShadow: "0px -2px 8px rgb(0 0 0 / 8%)",
    boxSizing: "border-box",
    position: "sticky",
    bottom: 0
});

export const ModalTitle = styled("div")((props) => ({
    width: "100%",
    height: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontSize: 24,
    fontWeight: 500,
    color: props.theme.textColor,
    backgroundColor: props.theme.backgroundColor
}));

export const CloseButton = styled(Button)((props) => ({
    marginRight: 8,

    // color: props.theme.textColor,
    backgroundColor: props.theme.borderColor
}));
export const ModalFooterInner = styled("div")((props) => ({
    width: "100%",
    height: "100%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: props.theme.backgroundColor
}));
