import styled from "@emotion/styled";
import { characterAvatarGroups } from "config/constants";
import {
    Counter,
    LinearProgressBar,
    TextField,
    Tooltip
} from "monday-ui-react-core";
import PropTypes from "prop-types";
import React, { useState } from "react";
import Color from "theme/colors";
import Modal from "../Modal";
import {
    Avatar,
    AvatarContainer,
    AvatarGroup,
    AvatarOption,
    AvatarWrapper,
    CharacterLevel,
    Layout,
    NameGroup,
    Section,
    TextFieldWrapper,
    TooltipWrapper
} from "./styled";

const StyleLinearProgressBar = styled(LinearProgressBar)((props) => ({
    "& > div ": {
        border: `1px solid ${props.theme.borderColor}`,
        borderRadius: "4px",
        boxSizing: "border-box",
        background: props.theme.progressBackground
    }
}));

function EditCharacterModal({ character, onClose, onSubmit }) {
    const [characterName, setCharacterName] = useState(
        character.name || character.user?.name
    );
    const [characterAvatar, setCharacterAvatar] = useState(
        character.avatar || "MaleSpearman1.png"
    );

    const handleSubmit = () => {
        onSubmit({
            id: character.id,
            name: characterName,
            avatar: characterAvatar
        });
    };

    return (
        <Modal
            open
            title='Edit Character'
            onClose={onClose}
            onSubmit={handleSubmit}>
            <Layout>
                <Section>
                    <TextFieldWrapper>
                        <TextField
                            title='Character name'
                            value={characterName}
                            onChange={(value) => {
                                setCharacterName(value);
                            }}
                        />
                    </TextFieldWrapper>
                    <AvatarContainer>
                        <Avatar avatar={characterAvatar} />
                        <CharacterLevel>
                            <Counter
                                count={character.level || 1}
                                kind={Counter.kinds.LINE}
                            />
                        </CharacterLevel>
                    </AvatarContainer>
                    <TooltipWrapper>
                        <Tooltip
                            content={`${character.xp} / ${character.levelXp}`}
                            position={Tooltip.positions.BOTTOM}>
                            <StyleLinearProgressBar
                                className='linear-progress-bar_small-wrapper'
                                size='large'
                                max={character.levelXp}
                                value={character.xp}
                                multi
                                multiValues={[
                                    {
                                        value: character.xp,
                                        color: Color.PRIMARY
                                    }
                                ]}
                            />
                        </Tooltip>
                    </TooltipWrapper>
                </Section>
                <Section>
                    {characterAvatarGroups.map(
                        (characterAvatarGroup, index) => (
                            <AvatarGroup key={index}>
                                <NameGroup>
                                    <span>{characterAvatarGroup.name}</span>
                                </NameGroup>

                                <AvatarWrapper>
                                    {characterAvatarGroup.characterAvatarSets.map(
                                        (characterAvatarSet) => {
                                            if (
                                                Number(character.level) <
                                                characterAvatarSet.requiredLevel
                                            ) {
                                                return characterAvatarSet.list.map(
                                                    (avatar) => (
                                                        <Tooltip
                                                            content={`Required level ${characterAvatarSet.requiredLevel}`}
                                                            key={avatar}
                                                            tip={false}>
                                                            <AvatarOption
                                                                avatar={avatar}
                                                                selected={
                                                                    avatar ===
                                                                    characterAvatar
                                                                }
                                                                disable
                                                            />
                                                        </Tooltip>
                                                    )
                                                );
                                            } else {
                                                return characterAvatarSet.list.map(
                                                    (avatar) => (
                                                        <AvatarOption
                                                            key={avatar}
                                                            avatar={avatar}
                                                            onClick={() => {
                                                                setCharacterAvatar(
                                                                    avatar
                                                                );
                                                            }}
                                                            selected={
                                                                avatar ===
                                                                characterAvatar
                                                            }
                                                        />
                                                    )
                                                );
                                            }
                                        }
                                    )}
                                </AvatarWrapper>
                            </AvatarGroup>
                        )
                    )}
                </Section>
            </Layout>
        </Modal>
    );
}

EditCharacterModal.propTypes = {
    character: PropTypes.object,
    onClose: PropTypes.func,
    onSubmit: PropTypes.func
};

export default EditCharacterModal;
