import { Button } from "monday-ui-react-core";
import PropTypes from "prop-types";
import React from "react";
import ReactModal from "react-modal";
import {
    CloseButton,
    ModalFooter,
    ModalFooterInner,
    ModalHeader,
    ModalTitle
} from "./styled";

const customStyles = {
    content: {
        padding: 0
    }
};

function Modal({ open, title, children, onClose, onSubmit }) {
    return (
        <ReactModal
            style={{
                ...customStyles
            }}
            isOpen={open}
            contentLabel={title}>
            <ModalHeader>
                <ModalTitle>{title}</ModalTitle>
            </ModalHeader>
            {children}
            <ModalFooter>
                <ModalFooterInner>
                    <CloseButton kind={Button.kinds.TERTIARY} onClick={onClose}>
                        Close
                    </CloseButton>
                    <Button onClick={onSubmit}>Submit</Button>
                </ModalFooterInner>
            </ModalFooter>
        </ReactModal>
    );
}

Modal.propTypes = {
    open: PropTypes.bool,
    title: PropTypes.string,
    children: PropTypes.node,
    onClose: PropTypes.func,
    onSubmit: PropTypes.func
};

export default Modal;
